<template>
  <div class="viproute-box">
    <p class="viproute-tip clearrix">
      提现
      <Button class="fr" type="primary" @click="$router.back()">返回</Button>
    </p>
    <Alert class="mb10" show-icon>您可以通过充值功能将资金转入可用余额，可用余额可以支付、兑换营销资源等，也可以自主提现</Alert>
    <div class="info-box">
      <p class="tip">账户资金</p>
      <Row class="content">
        <Col span="3">商户名称</Col>
        <Col span="6">{{accountInfo.account}}</Col>
      </Row>
      <Row class="content">
        <Col span="3">可用余额</Col>
        <Col span="6">
          <span class="redColor">{{accountInfo.balance}}</span>元
        </Col>
      </Row>
    </div>
    <div class="info-box">
      <p class="tip">提现信息</p>
      <Form
        class="cashout-box"
        ref="cashout"
        :model="cashoutForm"
        :rules="cashoutRules"
        :label-width="80"
      >
        <FormItem label="提现金额" prop="money">
          <Input type="text" v-model="cashoutForm.money" placeholder="输入提现金额">
            <span slot="append">元</span>
          </Input>
        </FormItem>
        <FormItem label="转入账号" prop="bankNo">
          <Select v-model="cashoutForm.bankNo">
            <Option
              v-for="(item, index) in accountInfo.cardList"
              :value="item.value"
              :label="item.label"
              :key="index"
              class="card-item clearfix"
            >
              <img :src="item.cardImg" alt />
              <span>{{item.cardFour}}</span>
            </Option>
          </Select>
        </FormItem>
        <FormItem label="备注" prop="userDesc">
          <Input type="textarea" v-model="cashoutForm.userDesc" />
        </FormItem>
        <FormItem label="提现密码" prop="payPwd">
          <Row>
            <Col span="19">
              <Input type="password" placeholder="请输入提现密码" v-model="cashoutForm.payPwd" />
            </Col>
            <Col span="4" offset="1">
              <router-link to class="grayColor">忘记密码</router-link>
            </Col>
          </Row>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="getMoney">确认提现</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      accountInfo: {
        account: "", //商户名称
        balance: "", //可用余额
        cardList: []
      },
      cashoutForm: {
        money: "", //提现金额
        bankNo: "", //提现卡号
        userDesc: "", //描述
        payPwd: "" //提现密码
      },
      cashoutRules: {
        money: [
          { required: true, validator: this.validateMoney, trigger: "blur" }
        ],
        bankNo: [
          { required: true, message: "请选择转入账户", trigger: "change" }
        ],
        payPwd: [{ required: true, message: "请输入提现密码", trigger: "blur" }]
      },
      isResult: true //提现结果
    };
  },
  created() {
    this.getWithdrawInfo();
  },
  methods: {
    // 获取用户账号信息
    getWithdrawInfo() {
      this.$api
        .withdrawInfo()
        .then(res => {
          let resData = res.data.data;
          // 用户账号
          this.accountInfo.account = resData.userName;
          this.accountInfo.balance = resData.money;
          resData.cards.map(item => {
            if (item.c_status == 0) {
              this.accountInfo.cardList.push({
                value: item.cardNo,
                label: item.cardName + item.cardFour,
                cardFour: item.cardFour,
                cardImg: item.cardImg
              });
            }
          });
        })
        .catch(err => console.log(err));
    },
    validateMoney(rule, value, callback) {
      if (value === "") {
        callback(new Error("请输入提取金额"));
      } else if (!Number(value)) {
        callback(new Error("请输入数字，最多保留小数点后两位"));
      } else {
        const re = /(0*)(\d+\.\d{2})(\d*)/;
        this.cashoutForm.money = value.replace(re, "$2");
        if (this.cashoutForm.money * 1 > this.accountInfo.balance * 1) {
          callback(new Error("提现金额大于可用余额"));
        } else {
          callback();
        }
      }
    },
    // 提现
    getMoney() {
      this.$refs.cashout.validate(valid => {
          this.$Modal.success({
                title: "提现成功",
                content: `您本次提现<span class='redColor'>${this.cashoutForm.money}</span>元至结算银行账户<span class='redColor'>${this.cashoutForm.bankNo}</span>已提交成功，请留意账户信息`,
                onOk: this.getWithdrawInfo
              });
        if (valid) {
          this.$api
            .withdraw(this.cashoutForm)
            .then(res => {
              this.$Modal.success({
                title: "提现成功",
                content: `您本次提现<span class='redColor'>${this.cashoutForm.money}</span>元至结算银行账户${this.cashoutForm.bankNo}已提交成功，请留意账户信息`
              });
            })
            .catch(err => console.log(err));
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/theme.less";
@import "~@/less/mixins.less";
.info-box {
  border: 1px solid @borderColor;
  padding-bottom: 20px;
  margin-bottom: 15px;
  line-height: 32px;
  .tip {
    background-color: @grayBg;
    border-bottom: 1px solid @borderColor;
    padding: 5px 20px;
  }
  .content {
    padding: 20px 20px 0;
  }
}
.cashout-box {
  padding: 20px 20px 0;
  width: 50%;
}
.card-item {
  img {
    float: left;
    height: 32px;
  }
  span {
    float: right;
    line-height: 32px;
  }
}
</style>



